import React, { Component } from 'react';
import styles from './dashboard.css';
import FormFields from '../widgets/FormFields/formFields';
import { Editor } from 'react-draft-wysiwyg';
import { EditorState, convertToRaw, convertFromRaw } from 'draft-js';
import { stateToHTML } from 'draft-js-export-html';
import { firebaseTeams, firebaseArticles, firebase } from '../../firebase';
import Uploader from '../widgets/FileUploader/Uploader';
import { placeholder } from '@babel/types';

class Dashboard extends Component {
    state = {
        editorState: EditorState.createEmpty(),
        postError: '',
        loading: false,
        formData: {
            author: {
                element: 'input',
                value: '',
                config: {
                    name: 'author_input',
                    type: 'text',
                    placeholder: 'Author'
                },
                validation: {
                    required: true
                },
                valid: false,
                touched: false,
                validationMsg: ''

            },
            title: {
                element: 'input',
                value: '',
                config: {
                    name: 'title_input',
                    type: 'text',
                    placeholder: 'Tittle'
                },
                validation: {
                    required: true
                },
                valid: false,
                touched: false,
                validationMsg: ''

            },
            body: {
                element: 'text_editor',
                value: '',
                valid: true
            },
            team: {
                element: 'select',
                value: '',
                config: {
                    name: 'team_input',
                    options: []
                },
                validation: {
                    required: true
                },
                valid: false,
                touched: false,
                validationMsg: ''

            },
            image: {
                element: 'image',
                value: '',
                valid: true
            },
        }

    }

    // fetching / looping kroz timove nakon mounta
    componentDidMount() {
        this.loadTeams();
    }

    loadTeams = () => {
        firebaseTeams.once('value')
            .then((snapshot) => {
                let team = [];

                snapshot.forEach((childSnasphot) => {

                    team.push({
                        id: childSnasphot.val().teamId,
                        name: childSnasphot.val().city

                    })
                })

                const newFormData = {...this.state.formData};
                const newElement = {...newFormData['team']};

                newElement.config.options = team;
                newFormData['team'] = newElement;

                this.setState({ 
                    formData: newFormData
                  });
            })
    }

    updateForm = (element, content = '') => {
        const newFormData = {
            ...this.state.formData
        }
        const newElement = {
            ...newFormData[element.id]
        }

        if (content === '') {

            newElement.value = element.event.target.value;

        } else {
            newElement.value = content;
        }

        if (element.blur) {
            let validData = this.validatione(newElement);

            newElement.valid = validData[0];
            newElement.validationMsg = validData[1];
        }

        newElement.touched = element.blur;


        newFormData[element.id] = newElement;

        this.setState({
            formData: newFormData
        });

    }

    validatione = (element) => {
        let error = [true, ''];

        if (element.validation.required) {
            const valid = element.value.trim() !== '';
            const message = `${!valid ? 'This field is required' : ''}`;
            error = !valid ? [valid, message] : error;
        }

        return error;
    }

    submitButton = () => (
        this.state.loading ?
            'Loading...'
            :
            <div>
                <button type='submit'>Add post</button>
            </div>
    )

    submitForm = (event) => {
        event.preventDefault();
        let dataToSubmit = {};
        let formIsValid = true;


        for (let key in this.state.formData) {
            dataToSubmit[key] = this.state.formData[key].value;
        }

        for (let key in this.state.formData) {
            formIsValid = this.state.formData[key].valid && formIsValid;
        }

        if(formIsValid) {
            
            this.setState({ 
                loading:true,
                postError: ''
              });

              firebaseArticles.orderByChild('id').limitToLast(1).once('value')
              .then((snapshot) => {
                  let articleID = null;

                  snapshot.forEach(childSnapshot => {
                      articleID = childSnapshot.val().id
                  })

                  dataToSubmit['date'] = firebase.database.ServerValue.TIMESTAMP;
                  dataToSubmit['id'] = articleID + 1;
                  dataToSubmit['team']  = parseInt(dataToSubmit['team'], 10);



                  firebaseArticles.push(dataToSubmit).then(article => {
                      this.props.history.push(`/articles/${article.key}`)
                  }).catch(e => {
                    this.setState({ 
                        postError:e.message
                        });

              })
              })

        } else {
            this.setState({
                postError: 'Something went wrong'
            });
        }
    }

    showError = () => (
        this.state.registerError !== '' ?
            <div className={styles.classError}>{this.state.registerError}</div>
            :
            ''
    )

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState
        });

        let contentState = editorState.getCurrentContent();
        let rawContent = convertToRaw(contentState);
        let html = stateToHTML(contentState);

        this.updateForm({ id: 'body' }, html);


    }

    storeFilename = (filename) => {
        this.updateForm({id: 'image'}, filename);
    }

    render() {
        return (
            <div className={styles.postCard}>
                <form onSubmit={this.submitForm}>
                    <h2>Add post</h2>
                    <Uploader   filename={(filename) => this.storeFilename(filename)} />
                    <FormFields id={'author'}
                        formData={this.state.formData.author}
                        change={(element) => this.updateForm(element)} />
                    <FormFields id={'title'}
                        formData={this.state.formData.title}
                        change={(element) => this.updateForm(element)} />
                    <Editor editorState={this.state.editorState}
                        wrapperClassName='myEditor-wrapper'
                        editorClassName='myEditor-editor'
                        onEditorStateChange={this.onEditorStateChange} />
                    <FormFields id={'team'}
                        formData={this.state.formData.team}
                        change={(element) => this.updateForm(element)} />

                    {this.submitButton()}
                    {this.showError()}
                </form>
            </div>
        );
    }
}

export default Dashboard;