import React, { Component } from 'react';
// import axios from 'axios'
// import {URL} from '../../../config';
import Button from '../Buttons/button';
import VideosTemplate from './videosTemplate';
import {firebaseLooper, firebaseTeams, firebaseVideos} from '../../../firebase';

import styles from './videosList.css';


class VideosList extends Component {
    state = { 
        teams:[],
        videos:[],
        start: this.props.start,
        amount: this.props.amount,
        end: this.props.start + this.props.amount

     }
     loadMore = () => {
         let end = this.state.end + this.state.amount;
         this.request(this.state.end + 1,end);
     }

     renderTitle = () => {
         return this.props.title ? 
         <h3><strong>STAR WARS</strong> Videos</h3>
         :
         null
     }

     renderButton = () => {
         return this.props.loadmore ? 
         <Button type='loadmore' 
                 loadMore={() => this.loadMore()} 
                 cta='Load more videos' /> 
         :
         <Button type='linkTo'
                 cta='More Videos' 
                 linkTo='/videos' /> 
     }
     
     renderVideos = () => {
         let template = null;

         switch(this.props.type){

            case 'card':
            template = <VideosTemplate  data={this.state.videos}
                                        teams={this.state.teams}/>
            break;
            default:
            template = null;
         }
         return template;
     }

     request = (start, end) => {
         if(this.state.teams.length < 1) {

           firebaseTeams.once('value')
           .then((snapshot) => {
               const teams = firebaseLooper(snapshot);
               this.setState({ 
                   teams
                  });


               firebaseVideos.orderByChild('id').startAt(start).endAt(end).once('value')
               .then((snapshot) => {
                   const videos = firebaseLooper(snapshot);
                   this.setState({ 
                       videos: [...this.state.videos, ...videos],
                       start,
                       end
                      });
               })
           })



            //  axios.get(`${URL}/teams`)
            //  .then(response =>{
            //      this.setState({
            //          teams: response.data
            //      })
            //  })
         }

        //  axios.get(`${URL}/videos?_start=${start}&_end=${end}`)
        //  .then(response => {
        //      this.setState({
        //          videos: [...this.state.videos, 
        //                   ...response.data],
        //          start,
        //          end
        //      })
        //  })
     }

     componentWillMount() {
         this.request(this.state.start, this.state.end);
     }

    render() { 
        return ( 
            <div className={styles.videoWrapper}>
                {this.renderTitle()}
                {this.renderVideos()}
                {this.renderButton()}
            </div>

         );
    }
}
 
export default VideosList;