import React, { Component } from 'react';
import styles from './signin.css';
import FormFields from '../widgets/FormFields/formFields';
import {firebase} from '../../firebase';


class Signin extends Component {
    state = {
        registerError: '',
        loading: false,
        formData: {
            email: {
                element: 'input',
                value: '',
                config: {
                    name: 'email_input',
                    type: 'email',
                    placeholder: 'Enter your email'
                },
                validation: {
                    required: true,
                    email: true
                },
                valid: false,
                touched: false,
                validationMsg: ''

            },
            password: {
                element: 'input',
                value: '',
                config: {
                    name: 'password_input',
                    type: 'password',
                    placeholder: 'Enter your password'
                },
                validation: {
                    required: true,
                    password: true
                },
                valid: false,
                touched: false,
                validationMsg: ''
            }
        }

    }
    validatione = (element) => {
        let error = [true,''];

        if(element.validation.required){
            const valid = element.value.trim() !== '';
            const message = `${!valid ? 'This field is required' : ''}`;
            error = !valid ? [valid,message] : error;        
        }
        if(element.validation.password){
            const valid = element.value.length > 8;
            const message = `${!valid ? 'Password must have 8 characters' : '' }`;
            error = !valid ? [valid,message] : error;
        }
        if(element.validation.email){
            const valid = /\S+@\S+\.\S+/.test(element.value);
            const message = `${!valid ? 'Must be a valid email' : ''}`;
            error = !valid ? [valid,message] : error;
        }

        return error;
    }

    updateForm = (element) => {
        const newFormData = {
            ...this.state.formData
        }
        const newElement = {
            ...newFormData[element.id]
        }

        newElement.value = element.event.target.value;

        if(element.blur){
            let validData = this.validatione(newElement);

            newElement.valid = validData[0];
            newElement.validationMsg = validData[1];
        }

        newElement.touched = element.blur;


        newFormData[element.id] = newElement;

        this.setState({
            formData: newFormData
        });

    }

    submitButton = () => (
        this.state.loading ?
        'Loading...' 
        :
        <div>
            <button onClick={(event) => this.submitForm(event,false)}>Register</button>
            <button onClick={(event) => this.submitForm(event,true)}>Log in</button>
        </div>
    )

    submitForm = (event,type) => {
          event.preventDefault();
          if(type !== null){
              let dataToSubmit = {};
              let formIsValid = true;

              for(let key in this.state.formData){
                  dataToSubmit[key] = this.state.formData[key].value;
              }

              for(let key in this.state.formData){
                  formIsValid = this.state.formData[key].valid && formIsValid;
              }

              if(formIsValid){
                  console.log(dataToSubmit);

                  this.setState({ 
                      loading: true,
                      registerError: ''
                      });
                  
                      if(type){
                        firebase.auth().signInWithEmailAndPassword(dataToSubmit.email,dataToSubmit.password)
                        .then(() => {
                            this.props.history.push('/');
                        }).catch(error => {
                            this.setState({ 
                                loading:false,
                                registerError: error.message
                              });
                        })

                      } else {
                          firebase.auth().createUserWithEmailAndPassword(dataToSubmit.email,dataToSubmit.password)
                          .then(() => {
                              this.props.history.push('/');
                          }).catch(error => {
                              this.setState({ 
                                  laoding:false,
                                  registerError: error.message
                                  });
                          })
                      }
              }
          }
    }

    showError = () => (
        this.state.registerError !== '' ? 
        <div className={styles.classError}>{this.state.registerError}</div>
        :
        ''
    )
    
    render() {
        return (
            <div className={styles.logContainer}>
                <form onSubmit={(event) => this.submitForm(event,null)}>
                    <h2>Welcome to StarWars news</h2>
                    <p>Login or Register</p>
                    <FormFields id={'email'}
                        formData={this.state.formData.email}
                        change={(element) => this.updateForm(element)} />
                    <FormFields id={'password'}
                        formData={this.state.formData.password}
                        change={(element) => this.updateForm(element)} />
                        
                    {this.submitButton()}
                    {this.showError()}

                </form>
            </div>

        );
    }
}

export default Signin;