import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import FontAwesome from 'react-fontawesome';
import {firebase} from '../../../firebase';
import styles from './sidenav.css';



const SideNavItems = (props) => {
    console.log(props);

    const items = [
        {
            type: styles.option,
            icon: 'home',
            text: 'Home',
            link: '/',
            login: ''

        },
        {
            type: styles.option,
            icon: 'newspaper',
            text: 'News',
            link: '/news',
            login: ''

        },
        {
            type: styles.option,
            icon: 'play',
            text: 'Videos',
            link: '/videos',
            login: ''

        },
        {
            type: styles.option,
            icon: 'fas fa-feather-alt',
            text: 'Dashboard',
            link: '/dashboard',
            login: false

        },
        {
            type: styles.option,
            icon: 'sign-in-alt',
            text: 'Sign-in',
            link: '/sign-in',
            login: true

        },
        {
            type: styles.so,
            icon: 'sign-out-alt',
            text: 'Sign-out',
            link: '/sign-out',
            login: false

        }       
    ]

    const element = (item,i) => (
        <div className={styles.option} key={i}>
            <Link to={item.link} className={styles.optiona}>
                <FontAwesome name={item.icon} className={styles.icon} />
                {item.text}
            </Link>
        </div>
    )

    const showItems = () => {
        return items.map((item,i) => {
          return item.login !== '' ? 
          restricted(item,i)
          :
          element(item,i)
        });
    }

    const restricted = (item,i) => {
        let template = null;
        
        if(props.user === null && item.login){
            template = element(item,i);
        }

        if(props.user !== null && !item.login){
            if(item.link === '/sign-out'){
                template = (
                    <div key={i} className={item.type}  onClick={() => {
                        firebase.auth().signOut().then(() => {
                            props.history.push('/');
                        })
                    }} >
                        <FontAwesome  name={item.icon} />
                        {item.text}
                    </div>
                )
            } else {
                template = element(item,i);
            }
        }

        return template;
    }



    return (
        <div>
            {showItems()}
        </div>
    );
}

export default withRouter(SideNavItems);