import React, { Component } from 'react';
import {firebaseArticles, firebaseLooper, firebase} from '../../../firebase';
import SliderTemplate from './sliderTemplate';


class NewsSlider extends Component {
    state = { 
       news: []
     }

     componentWillMount(){
         firebaseArticles.limitToFirst(3).once('value')
         .then((snapshot) => {
             const news = firebaseLooper(snapshot);

             news.forEach((item,i) => {
                 firebase.storage().ref('images').child(item.image).getDownloadURL()
                 .then(url => {
                     news[i].image = url;
                     this.setState({ 
                        news
                         });
                 })
             })
         })



        // rest api fetch
        //  axios.get(`http://localhost:3000/articles?_start=${this.props.start}&_end=${this.props.amount}`).then(response => {
        //      this.setState({
        //          news: response.data
        //      })
        //  })
     }

    render() { 
        return (  
               <div>
                  <SliderTemplate data={this.state.news} type={this.props.type} settings={this.props.settings}/>
              </div>
        );
    }
}
 
export default NewsSlider;