import React, { Component } from 'react';
// import axios from 'axios';
// import {URL} from '../../../../config';
import {firebaseDB, firebaseLooper, firebaseTeams, firebase} from '../../../../firebase';
import styles from '../../articles.css';
import Header from './header';


class NewsArticles extends Component {
    state = { 
         articles: [],
         team:[],
         imageURL: ''
     }

     componentWillMount(){


        firebaseDB.ref(`articles/${this.props.match.params.id}`).once('value')
        .then((snapshot) => {
            let articles = snapshot.val();


            firebaseTeams.orderByChild('id').equalTo(articles.team).once('value')
            .then((snapshot) => {
                const team = firebaseLooper(snapshot);

                this.setState({ 
                    team,
                    articles
                  })

                  this.getImageURL(articles.image);
            })

        })



        //  axios.get(`${URL}/articles?id=${this.props.match.params.id}`).then(response =>{
        //      let article = response.data[0];

        //      axios.get(`${URL}/teams?id=${article.team}`).then(response => {
        //          this.setState({
        //              articles: article,
        //              team: response.data
        //          })
        //      })
        //  })
     }

     getImageURL = (filename) => {
         firebase.storage().ref('images').child(filename).getDownloadURL()
         .then(url => {
             this.setState({ 
                 imageURL: url
                  });
         })
     }

    render() { 

        const article = this.state.articles;
        const team = this.state.team;

        return ( 
                <div className={styles.articleWrapper}>
                 <Header teamData={team[0]}
                         date={article.date}
                         author={article.author}
                 />
                 <div className={styles.articleBody}>
                   <h1>{article.title}</h1>
                   <div className={styles.articleImg} 
                        style={{
                            background: `url('${this.state.imageURL}')`
                        }}>
                   </div>
                   <div className={styles.articleText}
                         dangerouslySetInnerHTML={{
                             __html: article.body
                         }}>
                    </div>
                 </div>    
                 
                </div>
         );
    }
}
 
export default NewsArticles;