import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import axios from 'axios';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import {firebaseArticles, firebaseTeams, firebaseLooper } from '../../../firebase';
// import { URL } from '../../../config';
import Button from '../Buttons/button';
import CardInfo from '../CardInfo/cardInfo';
import styles from './news.css';

class NewsList extends Component {
    state = {
        teams: [],
        items: [],
        start: this.props.start,
        end: this.props.start + this.props.amount,
        amount: this.props.amount

    }

    componentWillMount() {
        this.request(this.state.start, this.state.end);
    }
    request = (start, end) => {
// teams
       if(this.state.teams.length < 1){
           firebaseTeams.once('value')
           .then((snapshot) => {
               const teams = firebaseLooper(snapshot);
               this.setState({ 
                   teams
                  });

                  firebaseArticles.orderByChild('id').startAt(start).endAt(end).once('value')
                  .then((snapshot) => {
                      const articles = firebaseLooper(snapshot);
                      this.setState({ 
                          items: [...this.state.items, ...articles],
                          start,
                          end
                          });
                  }).catch((e) => {
                      console.log(e);
                  })
           })

    




        //    axios.get(`${URL}/teams`)
        //    .then(response => {
        //        this.setState({
        //            teams: response.data,
        //            start,
        //            end
        //        })
        //    })
       }


// articles
        // axios.get(`${URL}/articles?_start=${start}&_end=${end}`).then(response => {
        //     this.setState({
        //         items: [...this.state.items,
        //         ...response.data]
        //     })
        // })
    }

    renderNews = (type) => {
        let template = null;

        switch (type) {
            case ('card'):
                template = this.state.items.map((item, i) => {
                    return (
                        <CSSTransition classNames={{
                            enter: styles.newsWrapper,
                            enterActive: styles.newsEnter
                        }}
                                       timeout={2000}
                                       key={i}>
                            <div>
                                <div className={styles.newsListItem}>
                                    <Link to={`/articles/${item.id}`} className={styles.link}>
                                         <CardInfo  teams={this.state.teams}
                                                    team={item.team}
                                                    date={item.date} 
                                         />
                                        <h2 className={styles.titl}>{item.title}</h2>
                                    </Link>

                                </div>
                            </div>
                        </CSSTransition>

                    )
                })
                break;
            case ('cardMain'):
             template = this.state.items.map((item, i) => {
                return (
                    <CSSTransition classNames={{
                        enter: styles.newsWrapper,
                        enterActive: styles.newsEnter
                    }}
                                   timeout={2000}
                                   key={i}>

                    <Link to={`/articles/${item.id}`}>
                    <div className={styles.flexWrapper}>
                     <div className={styles.left} style={{background: `url(/img/articles/${item.image})`}}>
                      <div></div>
                     </div>
                     <div className={styles.right}>
                     <CardInfo teams={this.state.teams} team={this.state.team} date={item.date} />
                     <h2>{item.title}</h2>
                     </div>

                    </div>
                    </Link>


                    </CSSTransition>
                )
             })
            break;
            default:
                template = null;
        }
        return template;
    }

    loadMore = () => {
        let end = this.state.end + this.state.amount;
        this.request(this.state.end + 1, end);
    }

    render() {
        return (
            <div>
                <TransitionGroup component='div' className='list'>
                    {this.renderNews(this.props.type)}
                </TransitionGroup>
                <Button type='loadmore'
                        loadMore={() => this.loadMore()}
                        cta='Load more news'
                />
            </div>
        );
    }
}

export default NewsList;